.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #2c3e50; /* Dark background for contrast */
  color: #ecf0f1; /* Light color for text */
  font-family: 'Arial', sans-serif;
}

h1 {
  margin-bottom: 40px; /* More space between the heading and the clock */
  font-size: 2.5rem;
  text-align: center;
}

.clock-container {
  position: relative;
  width: 300px;
  height: 300px;
  border: 4px solid #ecf0f1;
  border-radius: 50%;
  background-color: #34495e; /* Slightly lighter background for the clock face */
  display: flex;
  align-items: center;
  justify-content: center;
}

.number {
  position: absolute;
  font-size: 1.25rem;
  font-weight: bold;
  color: #ecf0f1;
}

.clock-hand {
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform-origin: bottom center;
}
